<template>
  <div class="row">
    <div class="col-md-12 card p-0">
      <div class="card-body p-0">
        <div class="row">
          <div class="col-lg-9 col-md-6 col-sm-6 col-6">
            <my-filter :rules="rules" v-on:filter-change="updateFilter"></my-filter>
          </div>
          <div class="col-lg-3 col-md-6 col-sm-6 col-6">
            <column-toggle
              v-model="columnDefs"
              @input="cols => this.$refs['table'].updateColumns(cols)"
            ></column-toggle>
          </div>
        </div>
        <div class="col-sm-12 p-0">
          <my-table
            ref="table"
            :columnDefs="columnDefs"
            v-bind:data-rows="menus"
            :actions="actions"
            :actionsTable="actionsTable"
            @sortChange="sortChange"
          />
        </div>
      </div>
    </div>
    <el-dialog title="Tạo menu" :visible.sync="dialogFormCreateVisible" width="30%" @keyup.enter.native="storeMenu">
      <el-form :model="formCreateMenu">
        <el-form-item label="Tiêu đề:">
          <el-input
            autocomplete="off"
            v-model="formCreateMenu.title"
            placeholder="Header, footer, sidebar,..."
            :class="errors.has('name')?'border-danger':''"
            v-validate="'required'"
            data-vv-name="name"
            data-vv-as="Tiêu đề"
          ></el-input>
          <span class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</span>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormCreateVisible = false">Đóng</el-button>
        <el-button type="primary" @click="storeMenu" :disabled="formCreateMenu.title?false:true">Tạo menu</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
  import { MessageBox, Message, Dialog, Button, Input, Form, FormItem } from 'element-ui'
  import MyTable from 'src/components/UIComponents/Table.vue'
  import MyFilter from 'src/components/UIComponents/Filter.vue'
  import menuSchemas from './menu-schemas'
  import dtHelper from 'src/helpers/datatable'
  import ColumnToggle from 'src/components/UIComponents/ColumnToggle'

  export default{
    components: {
      MyTable,
      MyFilter,
      ElForm: Form,
      ElFormItem: FormItem,
      ElButton: Button,
      ElInput: Input,
      ElDialog: Dialog,
      Message,
      ColumnToggle
    },
    computed: {
      menus() {
        let rows = this.$store.state.menus;
        return dtHelper.filterByRules(rows, this.filterOutput);
      }
    },
    data () {
      let initFiledArrays = ['id', 'title', 'handle', 'created_at', 'status'];
      let columnDefs = dtHelper.buildInitFields(menuSchemas, initFiledArrays);

      return {
        filterOutput: [],
        columnDefs: columnDefs,
        actions: [
          {
            type: 'primary',
            icon: 'nc-icon nc-ruler-pencil',
            callback: this.edit
          },
          {
            type: 'danger',
            icon: 'nc-icon nc-simple-remove',
            callback: this.remove
          },
        ],
        filter: {},
        rules: dtHelper.buildRules(menuSchemas),
        dialogFormCreateVisible: false,
        formCreateMenu: {
          title: ''
        },
        actionsTable: [
          {
            title: 'Ẩn',
            callback: this.inactiveAll
          },
          {
            title: 'Hiện',
            callback: this.activeAll
          },
          {
            title: 'Xóa',
            color: 'text-danger',
            callback: this.removeAll
          }
        ],
      }
    },
    mounted() {
      this.$store.dispatch('fetchMenus');

      this.$store.dispatch('setPageTitle', 'Menu');
      this.$store.dispatch('setCurrentActions', [{
        label: 'Tạo menu',
        type: 'primary',
        icon: '',
        callback: this.createMenu
      }])
    },
    methods: {
      edit(index, row){
        this.$router.push('/menus/' + row.id);
      },
      createMenu(){
        this.dialogFormCreateVisible = true;
      },
      storeMenu(){
        let self = this;
        this.$validator.validateAll().then(function (result) {
          if (result) {
            self.$store.dispatch('storeMenu', self.formCreateMenu).then(res => {
              Message({
                message: 'Tạo mới thành công',
                type: 'success'
              });
              self.dialogFormCreateVisible = false;
            }, res => {
              Message({
                message: res.message,
                type: 'error'
              });
            });;
          }
        })
      },
      remove(index, row){
        MessageBox.confirm('Bạn có chắc chắn xóa không?', 'Warning', {
          confirmButtonText: 'Đồng ý',
          cancelButtonText: 'Hủy bỏ',
          type: 'warning',
          center: true
        }).then(() => {
          this.$store.dispatch('removeMenu', row.id).then(res => {
            Message({
              type: 'success',
              message: 'Đã xóa menu'
            });
          });
        }).catch(() => {
          Message({
            type: 'info',
            message: 'Hủy bỏ'
          });
        })
      },
      updateFilter(filterOutput) {
        this.filterOutput = filterOutput;
      },
      inactiveAll(rows){
        this.updateStatus(rows, 'inactive');
      },
      activeAll(rows){
        this.updateStatus(rows, 'active');
      },
      removeAll(rows){
        this.updateStatus(rows, 'delete');
      },
      updateStatus(rows, status){
        let self = this;
        this.$util.updateStatusAll('menu', rows, status).then(function (result) {
          self.$store.dispatch('fetchMenus');
          Message({
            type: 'success',
            message: 'Cập nhật thành công'
          });
        }).catch(function (error) {
          Message({
            type: 'error',
            message: error.message
          });
        });
      },
      sortChange(data){
        let prop = data.prop;
        let order = data.order == 'ascending' ? 'asc' : 'desc';
        this.$store.dispatch('fetchMenus', {
          order: prop + '=' + order
        });
      },
    },
    destroyed(){
      this.$store.dispatch('setCurrentActions', []);
    }
  }
</script>
<style lang="scss">
  .el-table .td-actions{
  button.btn {
    margin-right: 5px;
  }
  }
</style>
